.Network-explorer {
    padding-bottom: 3px;
    margin: 3px 40px 3px 25px;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.65px;
    color: #ffffff;
    display: inline-block;
    position: absolute;
}

.Network-explorer:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: #ffffff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
    color: #ffffff;
}

.Network-explorer:active {
    color: #21a1e9;
}

.Network-explorer:hover:after {
    transform: scaleX(1);
    transform-origin: center;
    color: #21a1e9;
}

